.MuiPaginationItem-page.Mui-selected {
  background-color: #04724d !important;
  color: #fff !important;
  border-color: #04724d !important;
}

.MuiPaginationItem-outlined {
  background-color: #e1e5eb !important;
  border-color: #e1e5eb !important;
  color: #333 !important;
}

.pagination {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  padding: 20px 20px 20px 0;

  .iconDiv {
    cursor: pointer;

    .paginationIcon {
      background: transparent;
      border-radius: 50%;
      color: #fff;
    }
  }

  .iconDivDisabled {
    cursor: default;

    .paginationIcon {
      background: transparent;
      border-radius: 50%;
      color: #fff;
    }
  }
}

.iconDivDisabled {
  cursor: default;

  .paginationIcon {
    background: #ecf7fb;
    border-radius: 50%;
    color: #56c29e;
  }
}

.next-button {
  padding: 5px 10px;
  padding-top: 2px;
  padding-left: 14px;
  background: #04724d;
  color: #fff;
  border-radius: 4px;
  margin: 0 5px;
  display: flex;
  justify-content: center;
  align-items: center;
}
